<script>
import Head from '@/page/header/Head'
  export default {
    name: 'My',
    components:{Head},
    data() {
      return {
        user:'',
        flag:false,
        headPhoto:''
      }
    },
    beforeMount() {
      this.headPhoto = this.$store.state.login.headPhoto
    },
   created() {
     if(this.$store.state.login.show){
       var arr = JSON.parse(localStorage.getItem('vuex'))
      //  console.log(arr)
       this.user = arr.login.user
       this.flag=true
     }
   },
   methods: {
     acc(){
       // 跳转退出登录页面
       if(this.$store.state.login.show){
          this.$router.push('/Account')
       }else{
         this.$router.push('/Login')
       }
     }

   },
  }
</script>

<style lang="scss">
.my{
  width: 100%;
  height: 100%;
  background: #F5F5F5;
  .user{
    width: 100%;
    height:1.2rem;
    background:#3792E5;
    &>div{
      float: left;
      color: white;
    }
    .user_left{
      width: 20%;
      text-align: center;
      line-height: 1.1rem;
      img{
        width:0.8rem;
        height:0.8rem;
        border-radius:50%;
      }
    }
    .user_content{
      width: 70%;
      line-height: 0.3rem;
      font-size: 0.2rem;
    }
    .user_right{
