      margin-left:0.02rem;
      text-align:center;
      font-size:0.2rem;
      line-height:0.4rem;
      .my_top{
        text-align: center;
        margin-top: 0.15rem;
      }
    }
  }
  /* 余额优惠积分结束 */
  /* 业务开始 */
  .serve{
    width: 100%;
    height: 0.5rem;
    /* margin-top: 0.2rem; */
    background: white;
    padding-left:0.2rem;
    &>div{
      float: left;
      line-height:0.5rem;
      font-size:0.2rem;
    }
    .sv{
      width: 5%;
      text-align: center;
    }
    .s_right{
      width: 93%;
      height: 100%;
      border-bottom: 1px solid #F5F5F5;
      margin-left: 2%;
    }
  }
  /* 业务结束 */
